<template>
  <a-form-model
    ref="ruleForm"
    :model="form"
    :rules="rules"
    :label-col="labelCol"
    :wrapper-col="wrapperCol"
  >
    <a-form-model-item
      label="IP黑名单"
      prop="blackListIps"
      help="一行一个IP，被加入黑名单的IP将被禁止访问！可使用通配符，例如：202.101.20.*。"
    >
      <a-input v-model="form.blackListIps" type="textarea" rows="8" />
    </a-form-model-item>
    <a-form-model-item
      label="IP白名单"
      prop="whiteListIps"
      help="一行一个IP，只有列表中的IP地址允许访问系统！可使用通配符，例如：202.101.20.*"
    >
      <a-input v-model="form.whiteListIps" type="textarea" rows="8" />
    </a-form-model-item>
    <a-form-model-item :wrapper-col="{ span: 12, offset: 6 }">
      <a-alert message="警告：如果设置了白名单，必须将本机IP包含在内，否则将导致管理员无法访问系统！" type="warning" show-icon />
    </a-form-model-item>
    <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
      <a-button type="primary" @click="onSubmit">提交</a-button>
    </a-form-model-item>
  </a-form-model>
</template>
<script>
import { message } from 'ant-design-vue'
import { getIpBlacklist, editIpBlacklist } from '@/api/setting'
export default {
  data() {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 14 },
      other: '',
      form: {
        blackListIps: '',
        whiteListIps: '',
      },
      rules: {
        blackListIps: [{ required: true, message: '请输入ip地址', trigger: 'blur' }],
      },
    }
  },
  mounted() {
    this.getIpList()
  },
  methods: {
    async getIpList() {
      const data = await getIpBlacklist()
      this.form = data
    },
    onSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) {
          return false
        }
        editIpBlacklist(this.form).then((response) => {
          message.success('保存成功')
        })
      })
    },
    resetForm() {
      this.$refs.ruleForm.resetFields()
    },
  },
}
</script>
<style lang="less" scoped>
.tips {
  width: 31vw;
  margin-left: 269px;
  margin-bottom: 10px;
}
</style>